:root {
  --MAIN-COLOUR: #fafaf9;
  --BG-COLOUR: #e5e7eb;
  --TEXT-LIGHT: #a3a3a3;
  --ACCENT-1: #0ea5e9;
  --ACCENT-2: #facc15;
  font-family: "Roboto Flex", sans-serif;
  font-size: 1rem;
  line-height: 1.5;
}

body {
  background-color: var(--BG-COLOUR);
  height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1rem;
}

/* ANNOUNCE SECTION */

.announce-section {
  width: fit-content;
  background-color: var(--MAIN-COLOUR);
  gap: 1.5rem;
  padding: 0.5rem 1rem;
  border-radius: 1.5rem;
  box-shadow: 2px 3px 5px 0px #bcbcbc;
}

#announcement {
  text-transform: uppercase;
}

/* GAME SECTION */

.game-section {
  width: fit-content;
  background-color: var(--MAIN-COLOUR);
  display: flex;
  justify-content: space-evenly;
  gap: 1.5rem;
  padding: 0.5rem;
  border-radius: 1.5rem;
  box-shadow: 2px 3px 5px 0px #bcbcbc;
}

.gameboard-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.5rem;
  padding: 1rem;
}

.gameboard-wrapper > h2 {
  font-size: 1.2rem;
}

.gameboard {
  display: flex;
  flex-direction: column;
  border: 1px solid var(--TEXT-LIGHT);
}

.gameboard > div {
  width: fit-content;
  display: flex;
}

.cell {
  border: 1px solid var(--TEXT-LIGHT);
  width: 2rem;
  height: 2rem;
}

.active:hover {
  background-color: var(--BG-COLOUR);
}

.active:active {
  background-color: var(--TEXT-LIGHT);
}

.hit {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
}

.miss {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_2___});
}

/* SHIPS */

.ship0 {
  background-color: purple;
}

.ship1 {
  background-color: blue;
}

.ship2 {
  background-color: green;
}

.ship3 {
  background-color: gold;
}

.ship4 {
  background-color: red;
}

/* BUTTON SECTION */

.button-section {
  width: 50rem;
  display: flex;
  justify-content: space-around;
}

.button-section > div {
  width: fit-content;
  background-color: var(--MAIN-COLOUR);
  gap: 1.5rem;
  padding: 0.5rem 1rem;
  border-radius: 1.5rem;
  box-shadow: 2px 3px 5px 0px #bcbcbc;
}
